.product{
	margin: 50px auto;
	position:relative;
	font-size:0;
	width:calc(100% - 40px);
	max-width:1000px;
	overflow:hidden;
	height:743px;
	//border:green solid 1px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	
	img{
		
		max-width:100%;
	   width:100%;
	   position:relative;
	   top:50%;
	   transform: translatey(-50%);
	   //box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

	   
	}
	ul{
		
		bottom: 20px;
    	right: 20px;
    	position:absolute;
		li{
			display: block;
			margin-bottom:20px;
			width:70px;
			height:70px;
			img{
	    		width:100%;
	    		border:green solid 1px;
	    		&:hover{
	    			top:1px;
					transform:scale(1.2);
				}
			}
			a{
				text-decoration: none;
			}
		}
	}
	@include mq(0,1040px){
		margin:50px auto;
		max-width:800px;
		width:calc(100% - 40px);
		height:595px;
	}
	@include mq(0,840px){
		margin:30px auto;
		width:calc(100% - 40px);
		max-width:360px;
		height:290px;
		ul{
			display:none;
		}
	}
	

}