.carousel{
	background:grey;
	max-width: 1180px;
	width:calc(100% - 40px);
	margin: 20px auto;
	position:relative;
	height:120px;

	.cta_left{
		position:absolute;
		width:2.7%;
		height:120px;
		background: lightgrey;
		display: block;
		left:0%;
		text-decoration: none;

		i{
			color: green;
			position:absolute;
			top: 50%;
			left:50%;
			transform:translate(-50%,-50%) rotateY(180deg);
		}

		@include mq(0, 1180px){
			width:30px;
		}
	}

	.cta_right{
		position:absolute;
		width:2.7%;
		height:120px;
		background: lightgrey;
		display: block;
		text-decoration: none;
		right:0%;
		i{
			color: green;
			position:absolute;
			top: 50%;
			left:50%;
			transform:translate(-50%,-50%);
		}
		@include mq(0, 1180px){
			width:30px;
		}

	}

	@include mq(0, 1220px){
		width:calc(100% - 40px);
		margin: 40px auto 25px auto;
		}
}