.hero{
	margin:100px 0;
	background:no-repeat;
	background-size: cover;
	background-position:center,center;

		article{
		max-width: 1180px;
		width: 100%;
		margin: 0 auto 0 auto;
		text-align:center;
		
			i{
				font-size: 40px;
				margin-bottom: 35px;
				display: block;
				color: black;
				&.icon-heart{
				}
			}
			h1{	
				font-size: 50px;
				line-height: 1.6;
				text-transform: uppercase;
				color: black;
			}
			p{
				font-size: 18px;
				line-height: 1.6;
				color: black;
				font-style:italic;
			}
			@include mq(0, 960px){
				margin:0 auto 0 auto;
				h1{
					font-size:40px;
					line-height:1.2;
					margin:20px 20px 20px 20px;
				}
				p{
					font-size:16px;
					line-height:1.4;
					padding:10px;
					margin:20px 20px 20px 20px;

				}
			}
			@include mq(0, 680px){
				margin:0 auto 0 auto;
				h1{
					font-size:35px;
					line-height:1.2;
					margin:20px 50px;
				}
				p{
					font-size:14px;
					line-height:1.4;
					margin:20px 50px;
					padding:0;

				}
			}
		}
		.cta{
			margin:100px auto;
			text-align: center;

			a{
				border: 1px solid black;
				text-decoration: none;
				line-height: 45px;
				display: inline-block;
				text-transform: uppercase;
				color: green;
				padding: 0 20px 0 20px;
				font-size: 20px;
				box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
					&:hover{
						color:green;
						transform:scale(1.1);
					}
				@include mq(0, 680px){
					font-size: 16px;
					padding: 0 10px;
					line-height:36px;
				}	
			}
			@include mq(0, 1000px){
				padding:50px 0;
			}
			@include mq(0, 680px){
				margin:32px auto;
				padding:0;
			}
		}
		@include mq(0, 680px){
			margin:110px auto 150px auto;
		}


}